@import "../../styles/variables";

.container {
  width: 600px;
}

.rewardsContainer {
  background-color: transparentize($color: $primary-green, $amount: 0.7);
  padding: 1em;

  & hr {
    width: 100%;
    height: 1px;
    background-color: $primary-green;
    border: 0;
  }
}

.v1 {
  color: rgba(255, 255, 255, 0.8);

  a {
    color: inherit;
    transition: 0.2s all ease;
    text-decoration: none;

    &:hover {
      color: white;
      text-decoration: underline;
    }
  }
}
